<template>
    <div class="userForm">
        <h1>Welcome Back!</h1>
        <el-form ref="form" :model="form">
            <el-form-item label="Username">
                <el-input placeholder="Please enter username" v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="Password">
                <el-input type="password" placeholder="Please enter password" v-model="form.password"></el-input>
            </el-form-item>
            <el-form-item label="Username">
                <el-input placeholder="Please enter deviceID" v-model="form.deviceID"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button style="width:100%;" type="primary" @click="login">Login</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import axios from 'axios'
    export default {
        data() {
            return {
                form: {
                    name: '',
                    password: '',
                    deviceID: ''
                }
            }
        },
        methods: {
            login() {
                if (this.form.name == '') {
                    this.$message.error('Username cannot be empty!');
                    return false
                }
                if (this.form.password == '') {
                    this.$message.error('Password cannot be empty');
                    return false
                }
                let params = {
                    Username: this.form.name,
                    Password: this.form.password,
                    deviceID: this.form.deviceID
                }
                axios.post('https://picharger01.www.mplabcharger.org:9064/UserInfo/LoginCheck', params).then((res) => {
                    if (res.data == '1') {
                        this.$router.push('/home');
                    } else {
                        this.$message.error('Error Login, Please Checck Username and Password!');
                    }
                })
            }
        }
    }
</script>

<style lang="css" scoped>
.userForm {
    padding: 15px;
    height: 100%;
    margin-top: 100px;
}
</style>